.Landing {
  background-image: url("clock.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  background-position: 50% 80%;
  height: calc(100vh - 40vh);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.Landing_page {
  height: 100vh;
}

/* img {
  max-width: 100%;
  max-height: 100%;
  background-image: url("clock.jpg");
  background-repeat: no-repeat;
} */

.landing_title {
  color: #172b4d;
  text-shadow: 2px 2px 8px #9e9e9ead;
}

.landing-page {
  /* margin-top: 5vh; */

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.landing-page-title {
  /* margin-top: 5vh; */
  font-weight: bold;
  color: #172b4d;
  text-shadow: 2px 2px 8px #9e9e9ead;
}

.landing_text_box {
  width: 25vw;
  margin-top: 3vh;
  border: 1px solid #9e9e9ead;
  padding: 2px;
  border-radius: 5px;
  background-color: white;
  margin-left: 10px;
  margin-right: 10px;
}

@media (max-width: 400px) {
  .landing-page {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .landing-page-title {
    font-size: 20px;
  }
  .landing_text_box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 90vw;
  }
  .landing_title {
    font-size: 20px;
    padding: 5px;
  }
}



