.Quiz {
  padding: 20px;
  -webkit-box-shadow: 2px 2px 6px #172b4d;
          box-shadow: 2px 2px 6px #172b4d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 10px;
}
.Quiz:hover {
  background-color: lightblue;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.Quiz_h1 {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 30px;
}
.Quiz img {
  margin: 10px 0px;
  width: 64px;
}

.Quiz_button {
  margin-top: 20px !important;
  background-color: #ffab00 !important;
  color: #fff !important;
}

.Quiz_button_container {
  padding-bottom: 20px;
}

@media (max-width: 767px) {
  .Quiz_h1 {
    font-size: 20px;
  }
}



