.Header {
  position: relative;
  padding-top: 16px;
  background-color: #0747a6;
  color: white;
  height: 100vh;
  width: 125px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.Header_logo {
  font-size: 30px;
  font-weight: bold;
  position: relative;
  display: inline;
  top: 0;
  padding: 13px;
  top: 40%;
  letter-spacing: 1px;
}
.Header_top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 40px;
}

/* .unauth {
  left: 20px;
} */
.Header_nav {
  margin: 0 auto;
}
@media (max-width: 767px) {
  .Header {
    height: 75px;
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    float: none;
    padding-top: 0;
    padding-left: 13px;
  }
}



