.fill-in .inputbox-requiz,
.outer-box {
  padding: 5px 10px 5px 25px;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid black;
  margin: 10px;
}

.create {
  padding: 10px 30px;
  background: #ffab00;
  color: #fff;
  -webkit-box-shadow: 0px 0px 10px 0px #b8c3db44;
          box-shadow: 0px 0px 10px 0px #b8c3db44;
  border: #ffab00;
  border-radius: 8px;
  outline: none;
}
.create:focus {
  background: #ffab00;
}

.fill-in input[type="submit"] {
  background: #36b37e;
  font-size: 30px;
  -webkit-box-shadow: 0px 0px 10px 0px #b8c3db44;
          box-shadow: 0px 0px 10px 0px #b8c3db44;
  color: #fff;
  border: 1px solid #36b37e;
  border-radius: 50px;
}

.remove {
  background: #ff5630;
  font-size: 45px;
  -webkit-box-shadow: 0px 0px 10px 0px #b8c3db44;
          box-shadow: 0px 0px 10px 0px #b8c3db44;
  color: #fff;
  border: 1px solid #ff5630;
  border-radius: 50px;
}

.re-container {
  padding-top: 30px;
  width: 70vw;
  margin: auto;
  margin-left: 20vw;
}

.fill-in-text {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.fill-in {
  display: -ms-flexbox;
  display: flex;
  padding: 20px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.fill-in div {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-size: 20px;
}
.fill-in .inputbox-requiz {
  width: 100%;
}

.heading_requiz {
  text-align: center;
  font-size: 30px;
  color: #172b4d;
}

p {
  font-size: 20px;
}



