.profile-page {
  height: 90vh;
  /* background-color: rgb(104, 156, 151, 0.4); */
}

form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.container {
  position: relative;
  height: 25px;
  width: 150px;
  margin: 10px;
}

.form-input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  padding: 5px 10px 5px 25px;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid black;
}
.form-input:focus {
  -webkit-transition: 0.25s all;
  -o-transition: 0.25s all;
  transition: 0.25s all;
  border-bottom: 1px solid skyblue;
}
.container i {
  position: absolute;
  top: 25%;
  left: 5px;
  z-index: 2;
}

.submit input {
  padding: 10px 8px;
}



